import React, { Component, Fragment } from "react";
import Header from "./components_js/Header";
import Home from "./components_js/Home";
import ProjectMenu from "./components_js/ProjectMenu";
import ValveSelection from "./components_js/ValveSelection";
import "./App.css";



import { makeStyles } from '@material-ui/core/styles';

import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
//import { Button, Form, FormGroup, Label, Input, FormText } from 'reactstrap';



class App extends Component {
  constructor(props) {
    super(props);
    this.projectName = "xyz"
    this.state = {
      loggedIn: "Please log in",
      addedValves: []
    };
    this.setLoggedIn = this.setLoggedIn.bind(this)
    this.addValve = this.addValve.bind(this)

  }



  setLoggedIn(value) {
    this.setState({ loggedIn: value })
  }

  addValve(value) {
    let copyvalves = this.state.addedValves;
    let check = copyvalves.findIndex(valve => valve.id === value.id)
    console.log(check)
    if(check>=0){
      //let idx = copyvalves.findIndex(valve => valve.id === value.id)
      copyvalves[check].counter+=1
      this.setState({
        addedValves: copyvalves
      })
    }
    else{
      value.counter = 1;
      this.setState({
        addedValves: this.state.addedValves.concat([value])
      })
    }
  
  }

  render() {
    console.log(this.state)

    return (


      <div style={{ backgroundColor: "#D6DBDF" }} >
        <div style={{ backgroundColor: "#D6EAF8" }} className="header">
          <Header></Header>
          <AppBar position="static">
            <Toolbar>

              <Button onClick={() => this.setLoggedIn("Logged in")} color="inherit">{this.state.loggedIn}</Button>
            </Toolbar>
          </AppBar>
        </div>
        <div className="row">
          <div style={{ backgroundColor: '#FAFAFB' }} className="left">

            <ProjectMenu
              projectName={this.projectName}
              valves={this.state.addedValves} />

          </div>
          <div className="right">
            <ValveSelection
              addHandler={this.addValve} />
          </div>

        </div>
      </div>



    );
  }


}

export default App;