import React, { Component } from "react";
import { Table } from "reactstrap";
import NewComponentModal from "./NewComponentModal";

import ConfirmRemovalModal from "./ConfirmRemovalModal.js";

class ComponentList extends Component {
  //construtor(props)
  render() {
    //const { filterStr } = this.state;
    //console.log(this.props.sizes)
    const components = this.props.components;
    return (
      <Table dark>
        <thead>
          <tr>
            <th>Pk</th>
            <th>Code</th>
            <th>Standard</th>
            <th>Types</th>
            <th>Material</th>
            <th>Joining</th>          
            <th>Size</th>
            <th>Temperature</th>
            <th>Corosivity</th>          
            <th>Application</th>
            <th>Price</th>
            <th>Description</th>          
            <th></th>
          </tr>
        </thead>
        <tbody>
          {!components || components.length <= 0 ? (
            <tr>
              <td colSpan="12" align="center">
                <b>Table is empty</b>
              </td>
            </tr>
          ) : (
            components.map(component => (
              <tr key={component.id}>
                <td>{component.id}</td>
                <td>{component.code}</td>
                <td>{component.standard}</td>
                <td>{component.types}</td>
                <td>{component.material}</td>
                <td>{component.joining}</td>
                <td>{component.size}</td>
                <td>{component.temperature}</td>
                <td>{component.corosivity}</td>
                <td>{component.application}</td>
                <td>{component.coeff}</td>
                <td>{component.description}</td>
                {/* <td align="center">
                  <NewComponentModal
                    create={false}
                    component={component}
                    resetState={this.props.resetState}
                  />
                  &nbsp;&nbsp;
                  <ConfirmRemovalModal
                    pk={component.pk}
                    resetState={this.props.resetState}
                  />
                </td> */}
              </tr>
            ))
          )}
        </tbody>
      </Table>
    );
  }
}

export default ComponentList;