
import React, { Component } from 'react';
import { Button, Form, FormGroup, Label, Input, FormText } from 'reactstrap';

class ProjectName extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <Form>
                <FormGroup>
                    <Input type="text" name="projectName" id="projectName" placeholder="Type in project name"
                        disabled={false}
                        onKeyDown={e => {
                            if (e.key === 'Enter') { e.preventDefault(); e.target.disabled = true }
                            this.props.nameHandler(e.target.value);
                        }}
                        onKeyUp={e => {
                            if (e.key === 'Enter') { e.preventDefault(); e.target.disabled = true }
                            this.props.nameHandler(e.target.value);
                        }}
                        onClick={e => {
                            e.target.disabled = false
                        }}
                    />
                </FormGroup>


            </Form>
        );
    }
}

export default ProjectName;