import React from "react";
import { Button, Form, FormGroup, Input, Label } from "reactstrap";

import axios from "axios";

import { API_URL } from "../constants";

class NewComponentForm extends React.Component {
  state = {
    pk: 0,
    code: "",
    standard: "",
    types: "",
    material: "",
    joining: "", size: "",
    temperature: "",
    corosivity: "",
    application: "",
    price: "",
    description: ""
  };

  componentDidMount() {
    if (this.props.component) {
      const { pk, code, standard, types, material, joining, size, temperature, corosivity, application, price, description } = this.props.component;
      this.setState({  pk, code, standard, types, material, joining, size, temperature, corosivity, application, price, description });
    }
  }

  onChange = e => {
    this.setState({ [e.target.code]: e.target.value });
  };

  createComponent = e => {
    e.preventDefault();
    axios.post(API_URL, this.state).then(() => {
      this.props.resetState();
      this.props.toggle();
    });
  };

  editComponent = e => {
    e.preventDefault();
    axios.put(API_URL + this.state.pk, this.state).then(() => {
      this.props.resetState();
      this.props.toggle();
    });
  };

  defaultIfEmpty = value => {
    return value === "" ? "" : value;
  };

  render() {
    return (
      <Form onSubmit={this.props.component ? this.editComponent : this.createComponent}>
        <FormGroup>
          <Label for="code">Code:</Label>
          <Input
            type="text"
            name="code"
            onChange={this.onChange}
            value={this.defaultIfEmpty(this.state.code)}
          />
        </FormGroup>
        <FormGroup>
          <Label for="standard">Standard:</Label>
          <Input
            type="text"
            name="standard"
            onChange={this.onChange}
            value={this.defaultIfEmpty(this.state.standard)}
          />
        </FormGroup>
        <FormGroup>
          <Label for="types">Types:</Label>
          <Input
            type="text"
            name="types"
            onChange={this.onChange}
            value={this.defaultIfEmpty(this.state.types)}
          />
        </FormGroup>
        <FormGroup>
          <Label for="material">Material:</Label>
          <Input
            type="text"
            name="material"
            onChange={this.onChange}
            value={this.defaultIfEmpty(this.state.material)}
          />
        </FormGroup>
        <Button>Send</Button>
      </Form>
    );
  }
}

export default NewComponentForm;