
import React, { Component } from "react";
import { Col, Container, Row } from "reactstrap";
import { Table } from "reactstrap";
import {
    Card, CardImg, CardText, CardBody,
    CardTitle, CardSubtitle, Button
} from 'reactstrap';

import "./FinalSelection.css";

class FinalSelection extends Component {
    constructor(props) {
        super(props);
        this.state = {
            curr: 0
        }
        this.setcurr = this.setcurr.bind(this)

    }

    setcurr(e) {
        this.setState({ curr: e })
    }
    chooseColor(myvalves, key) {
        // console.log(this.state.curr, key, this.state.curr == key)
        // if (this.state.curr == key) {
        //     return "#0099FF" 
        // }
        // return "#D6DBDF"
        this.props.finisheHandler()
        document.getElementById("div" + key.toString()).style.backgroundColor = "#d6eaf8";
        document.getElementById("card" + key.toString()).style.backgroundColor = "#d6eaf8";
        document.getElementById("btn" + key.toString()).disabled = true;
        document.getElementById("btn" + key.toString()).innerHTML = "Selected";

        for (var step = 0; step < myvalves.length; step++) {
            if (myvalves[step].id != key) {
                document.getElementById("div" + myvalves[step].id.toString()).style.backgroundColor = "#D6DBDF";
                document.getElementById("card" + myvalves[step].id.toString()).style.backgroundColor = "#ffffff";
                document.getElementById("btn" + myvalves[step].id.toString()).disabled = false;
                document.getElementById("btn" + myvalves[step].id.toString()).innerHTML = "Select";

            }

        }

    }
    chooseImage(valves, n) {
        if (valves.length > n) {
            return valves[n].image;
        }
        return "placeholder.png";

    }

    chooseCode(valves, n) {
        if (valves.length > n) {
            return "Code: " + valves[n].code;
        }
        return "";
    }
    chooseMaterial(valves, n) {
        if (valves.length > n) {
            return "Material: " + valves[n].material[0];
        }
        return "";
    }
    chooseSize(valves, n) {
        if (valves.length > n) {
            return "Size: " + valves[n].size[0];
        }
        return "";
    }

    myClick = (x) => (
        console.log(x)
    )
    render() {
        console.log(this.state.curr)

        if (!this.props.visible) {
            return <h5></h5>;
        }
        let placeHolder = this.props.valves;
        placeHolder=placeHolder.sort((a,b)=>(-a.coeff+b.coeff));

        let myvalves = placeHolder
        myvalves = myvalves.slice(0, Math.min(3, myvalves.length));

        let textoptions = ["Top Choice", "OMV recommendation", "Standard"];
        textoptions = textoptions.slice(3 - myvalves.length, 3);
        console.log(textoptions);
        for (var step = 0; step < myvalves.length; step++) {
            myvalves[step].title = textoptions[step];
            myvalves[step].key = myvalves[step].id;
        }
        console.log(myvalves)
        return (
            myvalves.map(valve => (
                <Col sm="4">
                    <div id={"div" + valve.id.toString()}>
                        <h3>
                            {valve.title}
                        </h3>
                        <Card>
                            <CardImg src={valve.image} 
                            height = {300}
                            bordercolor='#333' />
                            <CardBody id={"card" + valve.id.toString()} >
                                <CardText>
                                    <Table>
                                        <tbody>
                                            <tr className="material">
                                                <tbody>

                                                    <td>
                                                        <h4>Code:</h4>
                                                    </td>
                                                    <td>
                                                        <h6>{valve.code}</h6>
                                                    </td>
                                                </tbody>
                                            </tr>
                                            <tr className="material">
                                                <tbody>

                                                    <td>
                                                        <h4>Material:</h4>
                                                    </td>
                                                    <td>
                                                        <h6>{valve.material}</h6>
                                                    </td>
                                                </tbody>
                                            </tr>

                                            <tr className="corosivity">
                                                <tbody>
                                                    <td>
                                                        <h4>Corosivity:</h4>
                                                    </td>
                                                    <td>
                                                        <h6>{valve.corosivity}</h6>
                                                    </td>
                                                </tbody>
                                            </tr>

                                            <tr className="corosivity">
                                                <tbody>
                                                    <td>
                                                        <h4>Type:</h4>
                                                    </td>
                                                    <td>
                                                        <h6>{valve.type}</h6>
                                                    </td>
                                                </tbody>
                                            </tr>

                                            <tr className="corosivity">
                                                <tbody>
                                                    <td>
                                                        <h4>Joining:</h4>
                                                    </td>
                                                    <td>
                                                        <h6>{valve.joining}</h6>
                                                    </td>
                                                </tbody>
                                                <tr className="corosivity">
                                                </tr>

                                                <tbody>
                                                    <td>
                                                        <h4>Size:</h4>
                                                    </td>
                                                    <td>
                                                        <h6>{valve.size}</h6>
                                                    </td>
                                                </tbody>
                                            </tr>

                                            <tr className="tableelement">
                                                <p align="centre">{valve.description}</p>
                                            </tr>
                                        </tbody>
                                    </Table>
                                </CardText>
                                <Button id={"btn" + valve.id.toString()} onClick={() => {
                                    this.chooseColor(myvalves, valve.id)
                                    this.props.setSelectedValve(valve)
                                }
                                }>Select</Button>
                            </CardBody>
                        </Card>


                    </div>
                </Col>
            )
            )

        );
        return (
            <Col>
                <div className="text-center">
                    <h5>
                        {this.props.text}
                    </h5>
                    <img
                        src={this.chooseImage(this.props.valves, this.props.n)}
                        width="450"
                        className="img-thumbnail"
                        style={{ marginTop: "20px" }}
                    />
                    <h5>
                        {this.chooseCode(this.props.valves, this.props.n)}</h5>
                    <h5>  {this.chooseMaterial(this.props.valves, this.props.n)}</h5>
                    <h5>  {this.chooseSize(this.props.valves, this.props.n)}
                    </h5>
                    <hr />
                </div>
            </Col>

        );
    }
}

export default FinalSelection;